<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="materialCode">
          <el-input v-model.trim="query.materialCode" clearable placeholder="物料编号" class="filter-item" style="width: 178px;" @keyup.enter.native="searchInit" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        row-key="materialId"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="false"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

  </div>
</template>

<script>
import QueryItemMixin from '@/mixins/queryItem'
import paginationPage from '@/mixins/paginationPage'
import tableHeadSet from '@/mixins/tableHeadSet'
import { getDetailListApi as getListApi } from '@/apis/modules/analysis/inventory'

export default {
  name: 'AnalysisInventoryDetail',
  mixins: [QueryItemMixin, tableHeadSet, paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        materialCode: ''
      },
      tableData: [],
      columnList: [],
      columnSets: [],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = {
        ...this.query,
        warehouseId: this.$route.params.id,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
            this.tableData = list || []
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
